
.rollToLeft-enter-active,
.rollToLeft-leave-active {
  transition: all 0.5s;
}
.rollToLeft-enter,
.rollToLeft-leave-active {
  opacity: 0;
  -webkit-transform: translateX(24px);
          transform: translateX(24px);
}
.rollToLeft-enter .inner,
.rollToLeft-leave-active .inner {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.rollToLeft-enter-active,
.rollToLeft-leave {
  opacity: 1;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}
.rollToLeft-enter-active .inner,
.rollToLeft-leave .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
}
.cartcontrol .cart-decrease .inner {
  line-height: 24px;
  padding: 6px;
  font-size: 24px;
  color: #00a0dc;
  transition: all 0.4s;
}
.cartcontrol .cart-count {
  display: inline-block;
  width: 12px;
  vertical-align: top;
  padding-top: 6px;
  line-height: 24px;
  font-size: 10px;
  text-align: center;
  color: #93999f;
}
.cartcontrol .cart-add {
  display: inline-block;
  line-height: 24px;
  padding: 6px;
  font-size: 24px;
  color: #00a0dc;
}


