
.shopcar {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
}
.shopcar .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
  font-size: 0;
  color: rgba(255,255,255,0.4);
}
.shopcar .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shopcar .content .content-left .logo-wrapper {
  display: inline-block;
  position: relative;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  vertical-align: top;
  border-radius: 50%;
  background: #141d27;
}
.shopcar .content .content-left .logo-wrapper .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #2b343c;
  text-align: center;
}
.shopcar .content .content-left .logo-wrapper .logo.highlight {
  background: #00a0dc;
}
.shopcar .content .content-left .logo-wrapper .logo .icon-shopping_cart {
  font-size: 24px;
  line-height: 44px;
  color: #80858a;
}
.shopcar .content .content-left .logo-wrapper .logo .icon-shopping_cart.highlight {
  color: #fff;
}
.shopcar .content .content-left .logo-wrapper .count {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  line-height: 16px;
  border-radius: 12px;
  background: #f01414;
  box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.4);
  color: #fff;
  font-size: 9px;
  font-weight: 700;
  text-align: center;
}
.shopcar .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  line-height: 24px;
  padding-right: 12px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,0.1);
  font-size: 16px;
  font-weight: 700;
  color: rgba(255,255,255,0.4);
}
.shopcar .content .content-left .price.highlight {
  color: #fff;
}
.shopcar .content .content-left .desc {
  display: inline-block;
  vertical-align: top;
  margin: 12px 0 0 12px;
  line-height: 24px;
  font-size: 10px;
}
.shopcar .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  width: 105px;
}
.shopcar .content .content-right .pay {
  height: 48px;
  line-height: 48px;
  font-size: 12px;
  font-weight: 700;
  text-align: center;
}
.shopcar .content .content-right .pay.not-enough {
  background: #2b333b;
}
.shopcar .content .content-right .pay.enough {
  background: #00b43c;
  color: #fff;
}


